  margin-bottom: 14px;
}

.taste .flip-card .back .content .taste-button-list .taste-button {
  color: var(--main-color);
  border: 1px solid var(--highlight-color);
  cursor: pointer;
  background: none;
  border-radius: 35px;
  padding: 6px 16px;
  font-size: 10px;
  line-height: 1.2;
  display: block;
}

.taste .flip-card .back .content .taste-button-list .taste-button.taste-active {
  color: var(--white-color);
  background: var(--highlight-color);
  border: 1px solid var(--highlight-color);
}

.taste .flip-card .back .content .taste-description {
  text-align: left;
  line-height: 18px;
}

.taste .flip-card:first-child .front {
  background-color: var(--highlight-color);
}

.taste .flip-card:first-child .front .content h2 {
  color: var(--white-color);
}

.taste .flip-card:nth-child(2) .front, .taste .flip-card:last-child .front {
  background-color: var(--white-color);
}

.taste .flip-card:nth-child(3) .front {
  background-color: var(--second-color);
  border: 1px solid var(--second-color);
}

.taste .flip-card:nth-child(3) .front .content h2 {
  color: var(--white-color);
}

.taste .taste-cards .flip-card {
  width: 100%;
  max-width: 335px;
  height: 270px;
  transform-style: preserve-3d;
  perspective: 1000px;
  cursor: pointer;
  display: flex;
  position: relative;
}

.taste .flip-card .front, .taste .flip-card .back {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  flex-direction: column;
  transition: transform 1.05s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.taste .flip-card .back {
  background-color: #fafafa;
  transform: rotateY(180deg);
}

.taste .flip-card:hover .front {
  transform: rotateY(-180deg);
}

.taste .flip-card:hover .back {
  transform: rotateY(0);
}

.taste .flip-card .content {
  width: 100%;
  height: 100%;
  overflow: hidden;
  transform: translateZ(100px)scale(.8);
}

